import {container} from "assets/jss/material-kit-react.js";
import {blackColor} from "../components/headerLinksStyle";
import {isMobile} from 'react-device-detect';
import background from 'assets/img/light-blue-bg.jpg'
import {BACKGROUND_COLOR} from "../../../../consts";

const componentsStyle = {
    container: {
        backgroundImage: `url(${background})`,
        width: '100%',
        minHeight: '100vh',
        display: 'flex',
        justifyContent: 'center'
    },
    container2: {
        width: isMobile ? window.innerWidth : 400,
        height: '100vh',
        maxHeight: '100vh',
        display: 'flex',
        flexDirection: 'column',
        backgroundColor: BACKGROUND_COLOR,
        position: 'relative',
    },
    brand: {
        color: '#0e559f',
        textAlign: "center",
        marginBottom: '10vh',
    },
    title: {
        fontSize: "4.2rem",
        fontWeight: "600",
        display: "inline-block",
        position: "relative"
    },
    subtitle: {
        fontSize: "1.313rem",
        margin: "10px 0 0"
    },
    main: {
        background: "#FFFFFF",
        position: "relative",
        zIndex: "3"
    },
    mainRaised: {
        margin: "-60px 30px 0px",
        borderRadius: "6px",
        boxShadow:
            "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)"
    },
    link: {
        textDecoration: "none"
    },
    textCenter: {
        textAlign: "center"
    },
    actions: {
        display: 'flex',
        justifyContent: 'space-evenly',
        alignItems: 'flex-start',
    },
    iconActions: {
        fontSize: 40,
        margin: 0,
    },
    action: {
        textAlign: "center",
        width: 170,
        height: 150,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        borderRadius: 20,
        color: '#0e559f',
        '&:hover': {
            backgroundColor: 'rgba(0,0,0,0.05)',
            boxShadow: '0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%)',
        },
        '&:active': {
            backgroundColor: 'rgba(0,0,0,0.1)',
        }
    },
    content: {
        flex: 1,
        padding: 16,
    }
};

export default componentsStyle;
