import React, {useEffect, useState} from "react";
import styles from 'assets/jss/info_user/rulePage'
import {makeStyles} from "@material-ui/core/styles";
import consts, {PRIMARY_COLOR} from "../../consts";
import Loading from "../../components/Loading";
import {useTranslation} from "react-i18next";

const useStyles = makeStyles(styles);

const defaultRule = '<div>-&emsp;Đăng ký dịch vụ: gói cước “Đấu giá”: 2MT, gói cước được gia hạn tự động hàng ngày.<br/> -&emsp;Tham gia đấu giá: mỗi ngày thuê bao được 5 lần đặt giá miễn phí cho các sản phẩm trong các phiên đấu giá, đặt giá thêm sẽ tính cước 1MT/1 lần. Cước phí được trừ từ tài khoản di động của thuê bao trên mạng Movitel<br/>-&emsp;Cách chơi đấu giá:<br/> •&emsp;Gửi tin nhắn SMS: đầu số 86156<br/>•&emsp;Tương tác USSD: đầu số 156<br/>•&emsp;Truy cập qua website của chương trình: http://adivinhala.co.mz</div>';

function RuleInfoPage(props) {
    const classes = useStyles();
    const {getGuide, rule, title} = props;
    const {t} = useTranslation('info');
    const [loading, setLoading] = useState(false);

    useEffect(() => {
        getGuide({
            isdn: "2096091021",
            gameCode: consts.GAME_CODE,
            language: 'en',
        }, () => {
            setLoading(false);
        })
    }, [])

    return (
        <div className={classes.container}>
            {loading ? <Loading/> : <>
                <span style={{color: PRIMARY_COLOR, fontSize: 13, fontWeight: 600,}}>{title}</span>
                <div className={classes.content} dangerouslySetInnerHTML={{__html: `${rule || defaultRule}`}}/>
            </>
            }
        </div>
    )
}

export default RuleInfoPage;
